/* ===== GLOBAL STYLES ===== */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
  }
  
  :root {
    --color-primary: #0A0A0A;
    --color-secondary: #ee7600;
    --color-white: #f8f8f8;
    --color-black: #000;
    --color-grey0: #f8f8f8;
    --color-grey-1: #dbe1e8;
    --color-grey-2: #b2becd;
    --color-grey-3: #6c7983;
    --color-grey-4: #454e56;
    --color-grey-5: #2a2e35;
    --color-grey-6: #12181b;
    --br-sm-2: 14px;
    --box-shadow-1: 0 3px 15px rgba(0,0,0,.3);
  }
  
  .light-mode {
    --color-primary: #FFFFFF;
    --color-secondary: #24BBA7;
    --color-white: #454e56;
    --color-black: #000;
    --color-grey0: #f8f8f8;
    --color-grey-1: #6c7983;
    --color-grey-2: #6c7983;
    --color-grey-3: #6c7983;
    --color-grey-4: #454e56;
    --color-grey-5: #f8f8f8;
    --color-grey-6: #12181b;
  }
  
  body {
    background-color: var(--color-primary);
    font-family: 'Poppins', sans-serif;
    font-size: 1.1rem;
    color: var(--color-white);
    transition: all .4s ease-in-out;
  }
  
  a {
    display: inline-block;
    text-decoration: none;
    color: inherit;
    font-family: inherit;
  }
  
  /* ===== LAYOUT STYLES ===== */
  .container {
    display: none;
    transform: translateY(-100%) scale(0);
    transition: all .4s ease-in-out;
    background-color: var(--color-primary);
    
    &.active {
      display: block;
      animation: appear 1s ease-in-out;
      transform: translateY(0) scaleY(1);
    }
  }
  
  @keyframes appear {
    0% { transform: translateY(-100%) scaleY(0); }
    100% { transform: translateY(0) scaleY(1); }
  }
  
  header, section {
    min-height: 100vh;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    padding: 5rem 10rem;
    color: var(--color-white);
    overflow: hidden;
  }
  
  /* ===== HEADER STYLES ===== */
  .header-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    min-height: 100vh;
    align-items: center;
    padding: 0 2rem;
  
    .left-header {
      display: flex;
      align-items: center;
      position: relative;
  
      .h-shape {
        transition: all .4s ease-in-out;
        width: 60%;
        height: 100%;
        background-color: var(--color-secondary);
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        clip-path: polygon(0 0, 40% 0, 75% 100%, 0% 100%);
      }
  
      .image {
        border-radius: var(--br-sm-2);
        height: 90%;
        width: 68%;
        margin-left: 2rem;
        margin-top: 2rem;
        height: 85%;
        width:75%;
        background-color: var(--color-black);
        transition: all .4s ease-in-out;
  
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          transition: all .4s ease-in-out;
          filter: grayscale(100%);
  
          &:hover {
            filter: grayscale(0);
          }
        }
      }
    }
  
    .right-header {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-right: 5rem;
  
      .name {
        font-size: 3rem;
        margin-bottom: 1.5rem;
        span { color: var(--color-secondary); }
      }
  
      p {
        margin: 1.5rem 0;
        line-height: 2.2rem;
        font-size: 1.2rem;
      }
    }
  }
  
  /* ===== ABOUT SECTION ===== */
  .about-container {
    display: block;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 3.5rem;
    padding-bottom: 5rem;
  
    .left-about {
      padding-right: 5rem;
      p {
        line-height: 2rem;
        padding: 1rem;
        color: var(--color-grey-1);
      }
      h4 {
        font-size: 2rem;
        text-transform: uppercase;
      }
    }
  }
  
  .about-stats {
    padding-bottom: 4rem;
    .progress-bars {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 2rem;
  
      .progress-bar {
        display: flex;
        flex-direction: column;
        .prog-title {
          text-transform: uppercase;
          font-weight: 500;
        }
        .progress-con {
          display: flex;
          align-items: center;
          .prog-text { color: var(--color-grey-2); }
          .progress {
            width: 100%;
            height: .45rem;
            background-color: var(--color-grey-4);
            margin-left: 1rem;
            position: relative;
            span {
              position: absolute;
              top: 0;
              left: 0;
              height: 100%;
              background-color: var(--color-secondary);
              transition: all .4s ease-in-out;
              width: 60%;
              &.html { width: 80%; }
              &.css { width: 75%; }
              &.js { width: 60%; }
              &.react { width: 60%; }
              &.java { width: 80%; }
              &.springboot { width: 70%; }
            }
          }
        }
      }
    }
  }
  
  .stat-title {
    text-transform: uppercase;
    font-size: 1.4rem;
    text-align: center;
    padding: 3.5rem 0;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      width: 40%;
      height: 1px;
      background-color: var(--color-grey-5);
      transform: translateX(-50%);
    }
  }
  
  .timeline {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    padding-bottom: 3rem;
    .timeline-item {
      position: relative;
      padding-left: 3rem;
      border-left: 1px solid var(--color-grey-5);
      .tl-icon {
        position: absolute;
        left: -27px;
        top: 0;
        background-color: var(--color-secondary);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        i { font-size: 1.3rem; color: var(--color-white); }
      }
      .tl-duration {
        padding: .2rem .6rem;
        background-color: var(--color-grey-5);
        border-radius: 15px;
        display: inline-block;
        font-size: .8rem;
        text-transform: uppercase;
        font-weight: 500;
      }
      h5 {
        padding: 1rem 0;
        text-transform: uppercase;
        font-size: 1.3rem;
        font-weight: 600;
        span {
          color: var(--color-grey-2);
          font-weight: 500;
          font-size: 1.2rem;
        }
      }
      p { color: var(--color-grey-2); }
    }
  }
  
  /* ===== PORTFOLIO SECTION ===== */
  .port-text {
    padding: 2rem 0;
    text-align: center;
  }
  
  .portfolios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    margin-top: 3rem;
    .portfolio-item {
      position: relative;
      border-radius: 15px;
      img {
        width: 100%;
        height: 300px;
        object-fit: cover;
        border-radius: 15px;
      }
      .hover-items {
        width: 100%;
        height: 100%;
        background-color: var(--color-secondary);
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        opacity: 0;
        transform: scale(0);
        transition: all .4s ease-in-out;
        h3 {
          font-size: 1.5rem;
          color: var(--color-white);
          margin-bottom: 1.5rem;
        }
        .icons {
          display: flex;
          justify-content: center;
          align-items: center;
          .icon {
            background-color: var(--color-primary);
            border-radius: 50%;
            width: 50px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 1rem;
            cursor: pointer;
            transition: all .4s ease-in-out;
            i {
              font-size: 1.5rem;
              color: var(--color-white);
              margin: 0 1rem;
            }
            &:hover {
              background-color: var(--color-white);
              i { color: var(--color-primary); }
            }
          }
        }
      }
      &:hover .hover-items {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
  
  /* ===== CONTACT SECTION ===== */
  .contact-content-con {
    display: flex;
    padding-top: 3.5rem;
    .personal-contact {
      flex: 2;
      h4 {
        margin-top: 1rem;
        font-size: 2rem;
        text-transform: uppercase;
      }
      .contact-info {
        .contact-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 1.5rem 0;
          p {
            margin: .3rem 0 !important;
            padding: 0 !important;
            font-size: 1.1rem;
          }
          .icon {
            display: grid;
            grid-template-columns: 40px 1fr;
            i {
              display: flex;
              align-items: center;
              font-size: 1.3rem;
              color: var(--color-secondary);
            }
          }
        }
      }
      .contact-icons {
        display: flex;
        margin-top: 2rem;
        .contact-icon {
          display: flex;
          a {
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--color-white);
            background-color: var(--color-grey-5);
            cursor: pointer;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            margin: 0 .4rem;
            transition: all .4s ease-in-out;
            &:hover {
              background-color: var(--color-secondary);
              i { color: var(--color-primary); }
            }
            i {
              font-size: 1.3rem;
              color: var(--color-white);
            }
          }
        }
      }
    }
    .user-contact {
      flex: 3;
      margin-left: 3rem;
      .input-control {
        margin: 1.5rem 0;
        input, textarea {
          border-radius: 30px;
          font-weight: inherit;
          font-size: inherit;
          font-family: inherit;
          padding: .8rem 1.1rem;
          outline: none;
          border: none;
          background-color: var(--color-grey-5);
          width: 100%;
          color: var(--color-white);
          resize: none;
        }
      }
      .i-c-2 {
        display: flex;
        :last-child { margin-left: 1.5rem; }
      }
      .submit-btn {
        display: flex;
        justify-content: flex-start;
      }
    }
  }
  
  /* ===== BUTTONS ===== */
  .btn-con {
    display: flex;
    align-self: flex-start;
  }
  
  .main-btn {
    border-radius: 30px;
    color: inherit;
    font-weight: 600;
    position: relative;
    border: 1px solid var(--color-secondary);
    display: flex;
    align-self: flex-start;
    align-items: center;
    overflow: hidden;
    .btn-text { padding: 0 2rem; }
    .btn-icon {
      background-color: var(--color-secondary);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      padding: 1rem;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      transform: translateX(100%);
      transition: all .4s ease-out;
      z-index: -1;
    }
    &:hover {
      transition: all .4s ease-out;
      &::before {
        width: 100%;
        height: 100%;
        background-color: var(--color-secondary);
        transform: translateX(0);
        transition: all .4s ease-out;
      }
    }
  }
  
  .resume-btn {
    @extend .main-btn;
    background-color: var(--color-secondary);
    .btn-icon i { color: var(--color-white); }
  }
  
  /* ===== THEME BUTTON ===== */
  .theme-btn {
    top: 5%;
    right: 3%;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: var(--color-grey-4);
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 3px 15px rgba(0,0,0,.3);
    transition: all .1s ease-in-out;
    z-index: 1000;
    &:active { transform: translateY(-3px); }
    i {
      font-size: 1.4rem;
      color: var(--color-grey-2);
      pointer-events: none;
    }
  }
  
  /* ===== CONTROLS ===== */
  .controls {
    position: fixed;
    z-index: 10;
    top: 50%;
    right: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: translateY(-50%);
    .control {
      padding: 1rem;
      cursor: pointer;
      background-color: var(--color-grey-4);
      width: 55px;
      height: 55px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: .7rem 0;
      box-shadow: var(--box-shadow-1);
      i {
        font-size: 1.2rem;
        color: var(--color-grey-2);
        pointer-events: none;
      }
    }
    .active-btn {
      background-color: var(--color-secondary);
      transition: all .4s ease-in-out;
      i { color: var(--color-white); }
    }
  }
  
  /* ===== TITLE STYLES ===== */
  .main-title {
    text-align: center;
    h2 {
      position: relative;
      text-transform: uppercase;
      font-size: 4rem;
      font-weight: 700;
      span { color: var(--color-secondary); }
      .bg-text {
        position: absolute;
        top: 50%;
        left: 50%;
        color: var(--color-grey-5);
        transition: all .4s ease-in-out;
        z-index: -1;
        transform: translate(-50%, -50%);
        font-weight: 800;
        font-size: 6.3rem;
      }
    }
  }
  /* Contact Form Submit Button */
.contact-content-con .submit-btn {
  display: flex;
  justify-content: flex-start;
  margin-top: 2rem;

  .main-btn {
    border: 1px solid var(--color-secondary);
    color: var(--color-white);
    
    &:hover {
      color: var(--color-white);
      
      .btn-icon {
        background-color: var(--color-white);
        color: var(--color-secondary);
      }
    }
  }
}
.contact-content-con .submit-btn .main-btn {
  .btn-text {
    padding: 0 1.8rem 0 2rem; // Matches resume button padding exactly
  }
}
/* ===== BUTTON STYLES ===== */
.main-btn {
  border-radius: 30px;
  font-weight: 600;
  position: relative;
  border: 1px solid var(--color-secondary);
  display: flex;
  align-self: flex-start;
  align-items: center;
  overflow: hidden;
  transition: all .4s ease-out;
  
  .btn-text {
    padding: 0 2rem;
    color: var(--color-white); // Ensures text is always visible
  }

  .btn-icon {
    background-color: var(--color-secondary);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 1rem;
    transition: all .4s ease-out;
    i { color: var(--color-white); }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(100%);
    transition: all .4s ease-out;
    z-index: -1;
  }

  &:hover {
    .btn-icon {
      background-color: var(--color-white);
      i { color: var(--color-secondary); }
    }
  }
}

/* Specific styles for resume button */
.resume-btn {
  @extend .main-btn;
  background-color: var(--color-secondary);
  &:hover {
    background-color: transparent;
  }
}

/* Contact form submit button */
.contact-content-con .submit-btn .main-btn {
  background-color: var(--color-secondary);
  &:hover {
    background-color: transparent;
  }
}
  @import './media';